@import url(https://fonts.googleapis.com/css?family=Source+Sans+Pro:300,400,300italic,400italic);
@import url(https://fonts.googleapis.com/css?family=Source+Sans+Pro:300,400,300italic,400italic);
html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}

article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block;
}

body {
  line-height: 1;
}

ol,
ul {
  list-style: none;
}

blockquote,
q {
  quotes: none;
}

q:before,
q:after {
  content: '';
  content: none;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

body {
  -webkit-text-size-adjust: none;
}

mark {
  background-color: transparent;
  color: inherit;
}

input::-moz-focus-inner {
  border: 0;
  padding: 0;
}

input,
select,
textarea {
  -webkit-appearance: none;
  appearance: none;
}

html {
  box-sizing: border-box;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

strong,
b,
h1,
h2,
h3,
h4,
h5,
h6 {
  font-weight: 400;
  color: #363636;
}

h1 {
  font-size: 2.4em;
  letter-spacing: -0.015em;
}

h2 {
  font-size: 1.8em;
  letter-spacing: -0.015em;
}

h3,
h4,
h5,
h6 {
  font-size: 1.25em;
  letter-spacing: -0.015em;
}
body{height:100vh;background-image:url(/first_pet_project/static/media/bg.0054024c.jpg);background-repeat:repeat, no-repeat;background-size:100% 100%;background-attachment:fixed}body,body input,body textarea,body select{font-family:'Source Sans Pro', sans-serif;font-weight:300;color:#777777;font-size:1.5em;line-height:1.5em}@media screen and (min-width: 1680px){body,body input,body textarea,body select{font-size:1.75em}}@media screen and (max-width: 1280px){body,body input,body textarea,body select{font-size:1.3em}}@media screen and (max-width: 1086px){body,body input,body textarea,body select{font-size:1.2em}}@media screen and (max-width: 736px){body,body input,body textarea,body select{font-size:1.1em}}@media screen and (max-width: 360px){body,body input,body textarea,body select{font-size:1em}}

.App{width:45em;margin:0 auto;max-width:100%;padding:1em 0;display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;-webkit-align-items:center;align-items:center;-webkit-justify-content:center;justify-content:center}.App__header{text-align:center;height:3.7em;cursor:default}.App__header a{position:relative;display:inline-block;color:#ffffff;width:1em;height:1em;line-height:0.9em;font-size:2.5em;margin:0 0.25em 0 0.25em;outline:0;transition:opacity .25s ease-in-out}.App__header a svg{font-size:0.8em}.App__header a:hover svg{fill:#fff;transition:fill .3s ease-in}.App__header a::after{content:'';display:block;position:absolute;left:37%;bottom:-.5em;margin-left:-.15em;border-bottom:solid 0em #ffffff;border-left:solid .3em transparent;border-right:solid .3em transparent;transition:border-bottom-width .25s ease-in-out}.App__header a.active svg{fill:#fff}.App__header a.active::after{border-bottom-width:.3em}.App__header svg{fill:#F1CDC2;transition:fill .3s ease-in}.App__main{width:100%;background-color:white;border-bottom:solid 1px #A98075}.App__footer{color:rgba(255,255,255,0.45);text-align:center;padding-top:2em;font-size:0.75em;margin-bottom:2em}.App__footer ul{display:-webkit-flex;display:flex;-webkit-flex-wrap:wrap;flex-wrap:wrap;-webkit-justify-content:space-evenly;justify-content:space-evenly;list-style:none}.App__footer ul a{color:#fff}.App__footer ul span{margin:0 1em}@media screen and (max-width: 1680px){.App{padding:2em 0;max-width:calc(100% - 6em)}}@media screen and (max-width: 1280px){.App{padding:1.5em 0;max-width:calc(100% - 8em)}}@media screen and (max-width: 1086px){.App{padding:1.2em 0;max-width:calc(100% - 8em)}}@media screen and (max-width: 736px){.App{padding:1em 0;max-width:calc(100% - 2em)}}@media screen and (max-width: 360px){.App{padding:1em 0;max-width:100%}}

.home-page{height:20em;display:-webkit-flex;display:flex;-webkit-flex-direction:row;flex-direction:row;-webkit-align-items:center;align-items:center}.home-page .content{padding:3.5em 2.5em;width:100%}.home-page a{text-decoration:none;position:relative;-webkit-flex-shrink:0;flex-shrink:0;width:17em;height:100%}.home-page a::before{content:'';position:absolute;top:0;left:0;width:100%;height:100%}.home-page a img{display:block;position:absolute;top:0;left:0;width:100%;height:100%;object-fit:cover;object-position:center;transition:-webkit-filter .3s ease-in;transition:filter .3s ease-in;transition:filter .3s ease-in, -webkit-filter .3s ease-in}.home-page a .arrow-right{display:block;position:absolute;right:0;top:50%;margin-top:-1.375em;width:2.75em;height:2.75em;background:rgba(0,0,0,0.75);color:#ffffff;text-align:center;line-height:2.75em;font-size:1.5em;z-index:1;transition:width .15s ease-in-out, padding-right .15s ease-in-out}.home-page a .arrow-right::before{position:relative;top:0.125em;color:#fff;content:'\25ba'}.home-page a .arrow-right span{display:none;text-indent:-9999px}.home-page a:hover img{-webkit-filter:blur(3px);filter:blur(3px);transition:-webkit-filter .3s ease-in;transition:filter .3s ease-in;transition:filter .3s ease-in, -webkit-filter .3s ease-in}.home-page a:hover .arrow-right{width:3em;transition:width .15s ease-in-out}@media screen and (max-width: 980px){.home-page a{height:25em;width:100%}}@media screen and (max-width: 980px){.home-page{-webkit-flex-direction:column;flex-direction:column;height:auto}}

.gallery-page{padding:.5em 2em;height:auto}.gallery-page .wrapper__gallery{display:-webkit-flex;display:flex;-webkit-flex-wrap:wrap;flex-wrap:wrap;grid-gap:1em;gap:1em;-webkit-justify-content:center;justify-content:center;padding:1em 0 1em 0;background-color:#f0e8e6;margin-bottom:2em}.gallery-page .wrapper__gallery .gallery__card{width:12em;height:8em;position:relative;border:1px solid transparent;border-top:solid 1px #949494;border-left:solid 1px #949494;-webkit-filter:drop-shadow(4px 4px 2px #949494);filter:drop-shadow(4px 4px 2px #949494)}.gallery-page .wrapper__gallery .gallery__card h4,.gallery-page .wrapper__gallery .gallery__card p,.gallery-page .wrapper__gallery .gallery__card a{display:none;position:absolute;color:#fff}.gallery-page .wrapper__gallery .gallery__card h4{top:.4em;left:.4em}.gallery-page .wrapper__gallery .gallery__card p{bottom:.4em;left:.4em}.gallery-page .wrapper__gallery .gallery__card a{bottom:.4em;right:.4em}.gallery-page .wrapper__gallery .gallery__card img{display:block;position:absolute;top:0;left:0;width:100%;height:100%;object-fit:cover;object-position:center;transition:-webkit-filter .3s ease-in;transition:filter .3s ease-in;transition:filter .3s ease-in, -webkit-filter .3s ease-in}.gallery-page .wrapper__gallery .gallery__card:hover img{-webkit-filter:brightness(0.4);filter:brightness(0.4);transition:-webkit-filter .3s ease-in;transition:filter .3s ease-in;transition:filter .3s ease-in, -webkit-filter .3s ease-in}.gallery-page .wrapper__gallery .gallery__card:hover>h4,.gallery-page .wrapper__gallery .gallery__card:hover>p,.gallery-page .wrapper__gallery .gallery__card:hover>a{display:block}@media screen and (max-width: 960px){.gallery-page .wrapper__gallery .gallery__card{width:10em;height:18em}}@media screen and (max-width: 845px){.gallery-page .wrapper__gallery .gallery__card{width:14em;height:18em}}@media screen and (max-width: 782px){.gallery-page .wrapper__gallery{margin-bottom:0}.gallery-page .wrapper__gallery .gallery__card{width:19em;height:23em}}.gallery-page .gallery-tabs{margin:1em 0 0 0;max-height:87.23%;overflow:hidden}.gallery-page .gallery-tabs>input[type="radio"]{display:none}.gallery-page .gallery-tabs>div{display:none}.gallery-page #tab-gallery-1:checked ~ #all__gallery,.gallery-page #tab-gallery-2:checked ~ #living-rooms__gallery,.gallery-page #tab-gallery-3:checked ~ #bedrooms__gallery,.gallery-page #tab-gallery-4:checked ~ #children-rooms__gallery{display:block}.gallery-page .gallery-tabs>label{display:inline-block;cursor:pointer;padding:.3em .5em}.gallery-page .gallery-tabs>label:hover{color:black}.gallery-page .gallery-tabs>input[type="radio"]:checked+label{color:black;font-weight:400;background-color:#f0e8e6}@media screen and (max-width: 769px){.gallery-page{padding:.5em 0 0 0}}



